import React, { FC } from 'react';
import s from './Avatar.module.scss';
import Person from '../../images/person.svg';

type AvatarProps = {
  url?: string;
  isSmall?: boolean;
};

export const Avatar: FC<AvatarProps> = ({ isSmall, url }) => {
  return (
    <>
      {url ? (
        <img
          src='url'
          className={`${s.image} ${isSmall && s.small}`}
          alt='avatar'
        />
      ) : (
        <div className={`${s.placeholder} ${isSmall && s.small}`}>
          <Person
            height={'60%'}
            width={'60%'}
          />
        </div>
      )}
    </>
  );
};